<template>
  <div class="example">
    <contextmenu ref="contextmenu" :theme="theme">
      <contextmenu-item>1</contextmenu-item>
      <contextmenu-item>2</contextmenu-item>
      <contextmenu-item>3</contextmenu-item>

      <contextmenu-submenu title="">
        <contextmenu-item>4</contextmenu-item>

        <contextmenu-item divider></contextmenu-item>

        <contextmenu-submenu title="">
          <contextmenu-item >5</contextmenu-item>
        </contextmenu-submenu>
      </contextmenu-submenu>
    </contextmenu>

    <div :class="['box', theme]" v-contextmenu:contextmenu>
      
    </div>
  </div>
</template>

<script>
  import {
    directive,
    Contextmenu,
    ContextmenuItem,
    ContextmenuSubmenu,
    ContextmenuGroup,
  } from '../src'
  export default {
    name: 'Stoneless',
    directives: {
      contextmenu: directive,
    },
    components: {
      Contextmenu,
      ContextmenuItem,
      ContextmenuSubmenu,
      ContextmenuGroup,
    },
    props: {
      theme: String,
    },
    methods: {},
  }
</script>

<style scoped>
  .box {
    width: 100%;
  }
</style>



