<template>
  <div class="example">
    <v-contextmenu ref="contextmenu" event-type="click" :theme="theme">
      <v-contextmenu-item @click="handleClick">1</v-contextmenu-item>
      <v-contextmenu-item @click="handleClick">2</v-contextmenu-item>

      <v-contextmenu-item divider></v-contextmenu-item>

      <v-contextmenu-submenu title="">
        <v-contextmenu-item disabled @click="handleClick">4</v-contextmenu-item>
        <v-contextmenu-item @click="handleClick">5</v-contextmenu-item>

        <v-contextmenu-submenu title="">
          <v-contextmenu-item @click="handleClick">6</v-contextmenu-item>
          <v-contextmenu-item @click="handleClick">7</v-contextmenu-item>
        </v-contextmenu-submenu>
      </v-contextmenu-submenu>

      <v-contextmenu-item @click="handleClick">3</v-contextmenu-item>
    </v-contextmenu>

    <v-contextmenu ref="dbContextmenu" event-type="dblclick" :theme="theme">
      <v-contextmenu-item @click="handleClick">1</v-contextmenu-item>
      <v-contextmenu-item divider></v-contextmenu-item>
      <v-contextmenu-item @click="handleClick">2</v-contextmenu-item>
      <v-contextmenu-item @click="handleClick">3</v-contextmenu-item>
    </v-contextmenu>

    <!-- <v-contextmenu ref="touchContextmenu" event-type="touchstart">
      <v-contextmenu-item>1</v-contextmenu-item>
      <v-contextmenu-item @click="handleClick">2</v-contextmenu-item>
      <v-contextmenu-item @click="handleClick">3</v-contextmenu-item>
    </v-contextmenu> -->

    <div :class="['box', theme]" v-contextmenu:contextmenu>
      
    </div>

    <div :class="['box', theme]" v-contextmenu:dbContextmenu>
      
    </div>

    <!-- <div :class="['box', theme]" v-contextmenu:touchContextmenu>
      
    </div> -->
  </div>
</template>

<script>
  export default {
    name: 'CustomTrigger',

    props: {
      theme: String,
    },

    methods: {
      handleClick (vm, event) {
        alert(`${vm.$slots.default[0].text}`)
      },
      handleShow () {
        console.log('show')
      },
    },
  }
</script>

<style scoped>
  .box {
    width: 100%;
  }
</style>



